.action-story-well {
  position: relative;
  background-color: #ffffff;
  margin-bottom: 32px;
  text-align: left;
  padding: 21px;
  box-shadow: 0px 0px 2px 2px rgba(0, 0, 0, 0.05);
  border-width: 0;
  border-radius: 10px;
  min-height: 150px;
}
.volunteers-story-well:before {
  content: "";
  position: absolute;
  background: #ffffff;
  top: 45px;
  height: 20px;
  width: 20px;
  box-shadow: 0px 0px 2px 2px rgba(0, 0, 0, 0.05);
  transform: rotate(45deg) skew(10deg, 10deg);
  border-radius: 3px;
}
.volunteers-story-well:after {
  content: "";
  position: absolute;
  background: #ffffff;
  width: 20px;
  height: 100%;
  top: 0;
  border-radius: 10px;
}
@media only screen and (max-width: 800px) {
  .volunteers-story-well,
  .companies-story-well,
  .red-companies-story-well,
  .action-story-well {
    margin-left: 0 !important;
    margin-right: 0 !important;
  }
  .volunteers-story-well:before {
    top: -7px !important;
    left: 50% !important;
    margin-left: -10px;
    transform: rotate(-45deg) skew(10deg, 10deg) !important;
  }
  .volunteers-story-well:after {
    width: 100% !important;
    height: 20px !important;
  }
}
