.flip-container {
  perspective: 1000;
  display: inline-block;
}
.flip-container.active .flipper {
  transform: rotateY(180deg);
}
.flip-container,
.front,
.back {
  width: 250px;
  height: 400px;
}
.flipper {
  transition: 0.6s;
  transform-style: preserve-3d;
  position: relative;
}
.front,
.back {
  backface-visibility: hidden;
  position: absolute;
  top: 0;
  left: 0;
}
.front {
  z-index: 2;
  transform: rotateY(0deg);
}
.back {
